<template>
  <div>
    <div class="root">
      Event
      {{ test_message }}
      <button @click="goBack">Go Back</button>
      <button @click="gotoHome">To Home</button>
      <router-view class="contents"></router-view>
    </div>
    <div :class="sender_bg_class">
      <WebRTCSender :class="sender_class" width="500" height="500" />
    </div>
  </div>
</template>

<script>
import WebRTCSender from "@/components/WebRTCSender.vue";
import { EventBus, EVENT_SET_EVENT_TYPE } from "../utils/event-bus";

export default {
  data() {
    return {
      sender_bg_class: "preset_bg_sender",
      sender_class: "preset_sender",
      test_message: ""
    };
  },
  components: {
    WebRTCSender
  },
  created() {
    EventBus.$on(EVENT_SET_EVENT_TYPE, path => {
      this.test_message = path;
      this.sender_class = "live_sender";
    });
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    gotoHome() {
      this.$router.push("/info");
    }
  }
};
</script>

<style scoped>
.contents {
  width: 100%;
  height: 500px;
  background-color: aquamarine;
}

.preset_bg_sender {
  position: absolute;
  top: 0;
  /* left: 0;
  right: 0; */
  bottom: 0;
  width: 100%;
  height: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.preset_sender {
  /* margin-right: 100px; */

  /* width: 100px; */
}

.live_sender {
  width: 200px;
  height: 200px;
}

.root {
  position: absolute;
  width: 100%;
  height: 100%;
}
</style>