const routes = [
  {
    path: "/",
    redirect: '/info'
  },
  {
    path: '/info',
    component: Info
  },
  {
    path: '/event',
    component: Event,
    children: [
      {
        path: 'preset',
        component: Preset,
        // beforeEnter: requireAuth
      },
      {
        path: 'live',
        component: Live,
        // beforeEnter: requireAuth
      }
    ]
  },
];

const router = new VueRouter({
  routes
});

export default router;
