<template>
  <video
    id="wowza-sender"
    autoplay
    playsinline
    muted
    controls
  ></video>
</template>

<script>
import * as Sender from "../utils/webrtc/publish";
import {
  EventBus,
  EVENT_PUBLISH_WEBRTC,
  EVENT_SHOW_SNACKBAR
} from "../utils/event-bus";

export default {
  created() {
    const onShow = message => {
      EventBus.$emit(EVENT_SHOW_SNACKBAR, message);
    };

    const onHide = () => {
      console.log("### onHide");
    };

    this.$nextTick(() => {
      console.log("Sender Created");
      Sender.initialize(onShow, onHide);
    });

    EventBus.$on(EVENT_PUBLISH_WEBRTC, () => {
      console.log("RECEIVE PUBLISH");
    });
  }
};
</script>

